import React from 'react';
import { Project } from './Project';

const projects = [
  {
    image: 'inspyr.png',
    url: 'http://www.inspyrstudios.com/',
    hoverTextHeader: 'Inspyr Studios',
    hoverText: 'React | Redux | LESS'
  },
  {
    image: 'mosaic.png',
    className: 'noClick',
    hoverTextHeader: 'Customer Service Tool',
    hoverText: 'Design | LESS | React | MobX'
  },
  {
    image: 'reck.png',
    className: 'noClick',
    hoverTextHeader: 'Real-Time Order Processing App',
    hoverText: 'Meteor | MongoDB | D3 | Heroku'
  },
];

const rowTwo = [
  {
    image: 'merch.png',
    className: 'noClick',
    hoverTextHeader: 'E-Commerce Search Ranking Tool',
    hoverText: 'React | Redux | Sortable'
  },
  {
    image: 'emclock.jpg',
    url: 'http://www.emclock.com/',
    hoverTextHeader: 'Technology Startup',
    hoverText: 'HTML | CSS | Bootstrap | PHP'
  },
];

export const Portfolio = () => {
  return (
    <div className="section raleway100 projects" id="projects">
  		<div className="container text-center">
  			<div>
  				<h2>PROJECTS</h2>
  			</div>
  		<div className="hr text-center"><hr/></div>
  		</div>
  		<div className="container portfolio">
  			<div className="row">
          {projects.map((p, i) => {
            const { image, url, hoverTextHeader, hoverText } = p;
            return(
              <Project
                key={i}
                image={image}
                url = {url}
                hoverTextHeader = {hoverTextHeader}
                hoverText = {hoverText}
              />
            )
          })}
        </div>
        <div className="row">
          {rowTwo.map((p, i) => {
            const { image, url, hoverTextHeader, hoverText } = p;
            return(
              <Project
                key={i}
                image={image}
                url = {url}
                hoverTextHeader = {hoverTextHeader}
                hoverText = {hoverText}
              />
            )
          })}
  			</div>
  		</div>
  	</div>
  )
};



